footer{
    margin: auto;
    padding: 5vh 16vw;
    background-color: var(--mainColor);
    text-align: center;
}

.boxFootNav{
    margin: 10vh 0;
}

.boxFootNav a img{
    margin-bottom: 5vh;
}

.footNav{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2vw;
}

.footNav a{
    color: var(--textColor);
}

.footNav a:hover{
    color: var(--secondaryColor);
    transition: .4s ease;
}